body {
  width: 100%;
  height: 100%;
}
.menu-toggle {
  color: $primary;
  cursor: pointer;
}

.slideout-menu {
  z-index: 100;
  position: fixed;
  opacity: 0;
  top: 0;
  bottom: 0;
  right: 0;
  z-index: 0;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  display: none;
  padding: 70px 0 0 0;
  background: #fff;
  color: #fff;
  -webkit-transition: all 100ms ease-out;
  -moz-transition: all 100ms ease-out;
  -ms-transition: all 100ms ease-out;
  -o-transition: all 100ms ease-out;
  transition: all 100ms ease-out;
  a {
    color: $primary;
  }

  ul {
    list-style: none;
    margin: 0;
    padding: 0;
    ul.sub-menu {
      li {
        a { padding-left: 40px;}
      }


    }
    li a{
      margin:0;
      text-align: right;
      display: block;
      padding: 10px;
      font-size: 1.3em;
      border-bottom: 1px solid;
      -webkit-transition: all 100ms ease-out;
      -moz-transition: all 100ms ease-out;
      -ms-transition: all 100ms ease-out;
      -o-transition: all 100ms ease-out;
      transition: all 100ms ease-out;
      i {
        width: 30px;
        line-height: 30px;
        text-align: center;
        border: 1px solid;

      }
    }
    li a:hover {

      padding-left: 20px;
      -webkit-transition: all 100ms ease-out;
      -moz-transition: all 100ms ease-out;
      -ms-transition: all 100ms ease-out;
      -o-transition: all 100ms ease-out;
      transition: all 100ms ease-out;
    }
  }

}

// .slideout-menu-left {
